body{
  height: 95vh;
  
}

.profile-card{
   width: 100%;
   height: 97vh; ;
  display: flex;
 
  align-items: center;
  justify-content: center;
  background-color:rgb(95, 214, 204);
 
}
.card{
  height: 450px;
  width: 300px ;
  display: grid;
  background-color:rgb(152, 150, 240);
  border-radius: 10px;
  align-items: center;
  justify-content: center;
  /* grid-template-rows: 1fr 100px 1fr;
  grid-template-areas: "row1" "row2" "row3"; */
  
}
/* Main information */

.pic{
 width: 140px;
  border-radius: 50%;
}
.info {
  font-size: 18px;
  font-weight: 700;
  color: rgb(65, 62, 62);
}
.welcome {
  text-align: center;
 
}
/* name and skills button */
.btns{

  border: none;
  border-radius: 10px;
  text-align: center;
  background-color: rrgb(152, 150, 240) ;
  
}
.btns button{
  padding: 1em ;
  border: none;
  width: 70px;
  line-height: 5px;
  margin-top: 0px;
  margin-left: 10px;
  text-align: center;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  background-color: rgb(191, 215, 230) ;
 
}
.btns button:hover{
  background-color: rgb(95, 214, 204);
}

 /*Like styling*/
.like{
  background-color:rgb(152, 150, 240) ;
  border-radius: 50%;
  border: none;
}
i{
  color: rgb(255, 0, 0);
  
}
.fas {
  padding: 10px;
  font-size: 30px;
  
}
.likeReaction{
  text-align: center;
}
/* social  icons*/

.social-container{ 
text-align: center;

}
a.social{
  margin: 0 1rem;

}
a.linkedin{
  color:blue;

}
a.github{
  color: black;
}
/* responsive */
@media screen and (max-width: 370px) {
  
  .card{
    height: 400px;
    width: 250px ;
    display: grid;
    background-color:rgb(152, 150, 240);
    border-radius: 10px;
    align-items: center;
    justify-content: center;
 
  }
  .pic{
    width: 100px;
     border-radius: 50%;
   }
}